import { PATH_PATTERNS } from '../../operation/constants.js';
import { getChangeStatus } from '../utils/getChangeStatus.js';
export function buildIndexNameDiffItem(tableId, indexId, before, after, operations) {
    const status = getChangeStatus({
        tableId,
        indexId,
        operations,
        pathRegExp: PATH_PATTERNS.INDEX_NAME,
    });
    if (status === 'unchanged')
        return null;
    const data = status === 'removed'
        ? before.tables[tableId]?.indexes[indexId]?.name
        : after.tables[tableId]?.indexes[indexId]?.name;
    if (data === undefined)
        return null;
    return {
        kind: 'index-name',
        status,
        data,
        tableId,
        indexId,
    };
}
